/* Estilos para la p√°gina de inicio */

.home-container {
  padding: 0;
  margin: 0;
  width: 100%;
}

/* Hero Section */
.hero {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 60px 10%;
  background: linear-gradient(135deg, #23a6d5, #23d5ab);
  color: white;
  border-radius: 0 0 20px 20px;
}

.hero-content {
  width: 50%;
}

.hero-content h1 {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 10px;
}

.hero-content h2 {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 20px;
}

.hero-content p {
  font-size: 1.2rem;
  margin-bottom: 30px;
}

.hero-buttons {
  display: flex;
  gap: 15px;
}

.hero-image {
  width: 40%;
  display: flex;
  justify-content: center;
}

.hero-image img {
  max-width: 100%;
  height: auto;
}

/* Features Section */
.features {
  padding: 80px 10%;
  background-color: #f8f9fa;
}

.section-title {
  text-align: center;
  font-size: 2.2rem;
  margin-bottom: 50px;
  color: #333;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
}

.feature-card {
  background-color: white;
  border-radius: 10px;
  padding: 30px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease;
}

.feature-card:hover {
  transform: translateY(-5px);
}

.feature-icon {
  font-size: 2.5rem;
  color: #23a6d5;
  margin-bottom: 20px;
}

.feature-card h3 {
  font-size: 1.5rem;
  margin-bottom: 15px;
  color: #333;
}

.feature-card p {
  color: #666;
  line-height: 1.6;
}

/* Subjects Section */
.subjects {
  padding: 80px 10%;
}

.subjects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}

.subject-card {
  background-color: white;
  border-radius: 10px;
  padding: 25px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.subject-card:hover {
  transform: translateY(-5px);
}

.subject-card h3 {
  font-size: 1.3rem;
  margin-bottom: 10px;
  color: #333;
}

.subject-card p {
  color: #666;
  margin-bottom: 10px;
}

.badge {
  background-color: #e6f7ff;
  color: #1890ff;
  padding: 3px 10px;
  border-radius: 12px;
  font-size: 0.8rem;
}

.subject-card.view-more {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f8f9fa;
  border: 2px dashed #ddd;
}

.subject-card.view-more a {
  color: #23a6d5;
  text-decoration: none;
  font-weight: 500;
}

.subject-card.view-more a:hover {
  text-decoration: underline;
}

/* CTA Section */
.cta {
  background: linear-gradient(135deg, #23d5ab, #23a6d5);
  padding: 80px 10%;
  text-align: center;
  color: white;
  border-radius: 20px;
  margin: 50px 10%;
}

.cta h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.cta p {
  font-size: 1.2rem;
  margin-bottom: 30px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

/* Responsive Styles */
@media (max-width: 992px) {
  .hero {
    flex-direction: column;
    text-align: center;
    padding: 40px 5%;
  }
  
  .hero-content {
    width: 100%;
    margin-bottom: 40px;
  }
  
  .hero-buttons {
    justify-content: center;
  }
  
  .hero-image {
    width: 70%;
  }
}

@media (max-width: 768px) {
  .features-grid {
    grid-template-columns: 1fr;
  }
  
  .subjects-grid {
    grid-template-columns: 1fr 1fr;
  }
  
  .section-title {
    font-size: 1.8rem;
  }
}

@media (max-width: 576px) {
  .hero-content h1 {
    font-size: 2.5rem;
  }
  
  .hero-content h2 {
    font-size: 1.5rem;
  }
  
  .subjects-grid {
    grid-template-columns: 1fr;
  }
  
  .cta h2 {
    font-size: 2rem;
  }
}
